 app.post('/login', function(req, res, next) {
   console.log('SERVER -> Logging in ', req.body)

   if (req.body.app)
   {
     sequelize.query("select top 1 * from users where email ='" + req.body.username + "'").spread((users ,metadata)=>
     {
       if (users.length > 0) {
           console.log('Is reg -> user has registered')
           /*
           This is where I would normally return OK, indicating the user can continue with logging in. So lets actually log in.
           */
           passport.authenticate('local', { session: false }, function (err, user, info) {
               if (err) return next(err);
               console.log("User is ", user)
               if (user < 0 || !user) res.send({resp:'missing credentials'})
               else {
                 console.log("Login - all good, user found")
                 const expiresIn = 60 * 60 * 24 * 7; // 180 days
                 const token = jwt.sign(user, auth.jwt.secret, { expiresIn });
                 res.cookie('id_token', token, { maxAge: 1000 * expiresIn, httpOnly: true });
                 res.send({resp:'ok',id:user.id});

               }
           }) (req, res, next);

       } else
       {
         console.log("Is reg -> User not allowed to register")
         res.send({resp:'fail'})

       }
     });

   } else {
     sequelize.query("select top 1 * from users where email ='" + req.body.username + "'").spread((users ,metadata)=>
     {
         if (users.length > 0) {
           passport.authenticate('local', { session: false }, function (err, user, info) {
             if (err)
               return next(err);

             if (info)
             {
               let errorString = encodeURIComponent(info.message);
               return res.redirect('/login?error='+errorString);
             }

             if (!user)
             {
                res.redirect('/login');
              } else
             {

               const expiresIn = 60 * 60 * 24 * 7; // 180 days
               const token = jwt.sign(user, auth.jwt.secret, { expiresIn });
               res.cookie('id_token', token, { maxAge: 1000 * expiresIn, httpOnly: true });
               res.redirect('/');
             }

           })(req, res, next);
        }
        else{
           console.log("User does not exist");
        }
  })
   }

});





