import { create } from "apisauce"
//import moment from 'moment'
import history from './../core/history';


const api = create({
  "baseURL": "/api/",
  timeout: 10000
})



const onResponse = function(response) {
    //console.log('response' ,response)
  // if (response.ok && response.data.status === "ok") {
  //   return response.data.data
  // } else {
  //   throw new Error(response.data.data)
  // }
  if(response.status === 401)
  {
    history.push('/login');
  }
  if(response.status === 200)
    return response.data;
}



export default class Api {

  static logout() {
      api.post("/logout");
  }

  static Register(user) {
      return api.post("/user/register", user).then(onResponse);
  }

  static userProfile(){
      return api.get("/user/profile").then(onResponse);
  }

  static patientList(){
      return api.get("/patient/list" ).then(onResponse);
  }


  static searchPatient(id_number){
      return api.get("/patient/search" , {
        id_number:id_number
      }).then(onResponse);
  }

  static linkPatient(patient){
    return api.post("/patient/link" , patient ).then(onResponse);
  }


  static patientProfile(id){
      return api.get("/patient/profile" , {
        id:id
      }).then(onResponse);
  }

  static medicalLog(id){
      return api.get("/patient/medicallog" , {
        id:id
      }).then(onResponse);
  }

  static logInjury(details){
      return api.post("/patient/log" , details).then(onResponse);
  }

    static allSessions(id){
        return api.get("/session/all" , {
          id:id
        }).then(onResponse);
    }
   
    static allSessionsgraph(id){
      return api.get("/session/sum" , {
        id:id
      }).then(onResponse);
  }

  static getMaxangle(id){
    return api.get("/session/maxangle" , {
      id:id
    }).then(onResponse);
} 

 static getFrequencyPerWeek(id){
  return api.get("/session/freqweek" , {
    id:id
  }).then(onResponse);
}

    static allCycles(id){
        return api.get("/cycles/all" , {
          id:id
        }).then(onResponse);
    }

    static searchForgot(email){
        return api.get("/forgot/security" , {
          email:email
        }).then(onResponse);
    }

    static updateNewPassword(password, id){
        return api.post("/update/password" , {
          password:password,
          user_id:id
        }).then(onResponse);
    }
    static getSecurityQuestions(){
        return api.get("/security/questionlist" ).then(onResponse);
    }



  // static downloadInvoice(invoiceId) {
  //   const token = localStorage.getItem("token")
  //   window.open((process.env.REACT_APP_API_URL || "https://api.nelisat.skeg.com") + `/invoice/download/${invoiceId}?token=${token}`)
  // }
}



