import passport from 'passport';
//import { Strategy as FacebookStrategy } from 'passport-facebook';
//import { User, UserLogin, UserClaim, UserProfile } from '../data/models';
import { Strategy as LocalStrategy } from 'passport-local';
import { auth as config } from '../config';
import {findUser,getUserMap} from '../data/sequelize.js'

var passwordHash = require('password-hash');
var crypto = require('crypto');

function generateHash(password) {
  let iterations = 1;
  let salt = '767684f2';
  let algorithm = 'sha1'
  try {
    var hash = password;
    for(var i=0; i<iterations; ++i) {
      hash = crypto.createHmac('sha1', salt).update(hash).digest('hex');
    }

    //return algorithm + '$' + salt + '$' + iterations + '$' + hash;
    return (algorithm + '$'  + '$'  + '$' + hash);
  } catch (e) {
    throw new Error('Invalid message digest algorithm');
  }
}


passport.use(new LocalStrategy(
  async function(username, password, done) {

    var user  = await findUser(username);
    if (!user) return done(null, -1, null);
    var password_Hashed = '';

    if(passwordHash.isHashed(password))
    {
    
        password_Hashed= req.body.password;

      }
    else
    {
      password_Hashed = generateHash(password);

    }


    if(user.password != password_Hashed)
    {

      return done(null, false);
    }


  //  var customers  = await getUserMap(user.id);
    var retUser = user;
    retUser.password = null;
    //retUser.customers = customers;

    return done(null, retUser, null);
    // console.log('username',username)
    // User.findOne({ username: username }, function (err, user) {
    //   if (err) { return done(err); }
    //   if (!user) { return done(null, false); }
    //   if (!user.verifyPassword(password)) { return done(null, false); }
    //   return done(null, user);
    // });
  }
));


export default passport;



