/* eslint-disable camelcase */
/* eslint-disable object-curly-spacing */
/* eslint-disable comma-spacing */
/* eslint-disable linebreak-style */
const {PropTypes} = React;
import React, { Component } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from '../styles.css';
import {
  ButtonGroup,FormGroup, ControlLabel,Checkbox,
  Panel, Button,Breadcrumb,
} from 'react-bootstrap';

import { BootstrapTable, TableHeaderColumn , SearchField} from 'react-bootstrap-table';
import Api from '../../../api/client';

import {arrayAddRemove , filterData ,viewAll ,checked, transform_time , pain_rating,pain_rating_color, convertSecToHM,convertSecToH} from '../../../core/Utilities';

import {
  Tooltip,
  XAxis, YAxis, Area, LineChart, Line,
  CartesianGrid, AreaChart, Label } from 'recharts';
import { isNullOrUndefined } from 'util';

const CustomTooltipSummary  = React.createClass({
  propTypes: {
    type: PropTypes.string,
    payload: PropTypes.array,
    label: PropTypes.string,
  },

  render() {
    const { active } = this.props;

    var show=true;
    if (active) {
      const { payload, label } = this.props;

      if ((payload[0]) === undefined || (payload[0] === null)){
        show =false;
        return null;
      }
      else{
        return (
            <div className="custom-tooltip">
              <p className="intro">{`${payload[0].payload.name}`}</p>
              <p className="intro">{`Maximum Angle: ${payload[0].value}°`}</p>
              <p className="intro">{`Minumum Angle: ${payload[1].value}°`}</p>
              <p className="intro">{`Session Duration: ${payload[0].payload.ses_dur}`}</p>
              <p className="intro">{`Date: ${payload[0].payload.timestamp}`}</p>
            </div>
        );
      }
    }

    return null;
  }
});

const CustomTooltipSession  = React.createClass({
  propTypes: {
    type: PropTypes.string,
    payload: PropTypes.array,
    label: PropTypes.string,
  },

  render() {
    const { active } = this.props;
    if (active) {
      const { payload, label } = this.props;
      if((payload !=undefined) ||(payload !=null)){
        return (
          <div className="custom-tooltip">
            <p className="intro">{`Cycle:  ${label}`}</p>
            {payload.map(element => (
              <p className="intro">{`${element.name} :${element.value}°`}</p>
            ))}
          </div>
        );
      }
      else{
        return null;
      }
    }
    return null;
  }
});

const CustomTooltipCumulative  = React.createClass({
  propTypes: {
    type: PropTypes.string,
    payload: PropTypes.array,
    label: PropTypes.string,
  },

  render() {
    const { active } = this.props;
    if (active) {
      const { payload, label } = this.props;

        return(
            <div className="custom-tooltip">
              <p className="intro">{`${payload[0].payload.name}`}</p>
              <p className="intro">{`Total Duration: ${payload[0].payload.cumulative_duration_string}`}</p>
            </div>
        );
    }
    return null;
  }
});


class Session extends Component {
  constructor(props) {
    super(props);
    this.state = {
      sessions:[],
      personal_details:[],
      cycles:[],
      patient_id:-1,
      height:-1,
      width:-1,
      top:-1,
      botton:-1,
      left:-1,
      right:-1,
      summary:true,
      session:false,
      rSelected:[],
      title:'',
      sessionSelected:[],
      opacity:1,
      data:[],
      all:true,
      total_num_sessions : 0,
      max_session_angle : 0,
      sessiongraph:[],
      session_frequency : '',
      average_session_dur :'',
      cumulative_duration:'',
      last_session_date:'',

    };

    this.getSession = this.getSession.bind(this);
    this.getCycles = this.getCycles.bind(this);
    this.getProfile = this.getProfile.bind(this);
    this.getMaxangle = this.getMaxangle.bind(this);
    this.getSessionsToPlot = this.getSessionsToPlot.bind(this);
    this.setMargins = this.setMargins.bind(this);
    this.viewSession = this.viewSession.bind(this);
    this.viewSummary = this.viewSummary.bind(this);

    this.ClickOnSessionList = this.ClickOnSessionList.bind(this);
    this.OnClickSelectAll = this.OnClickSelectAll.bind(this);
  }

  componentDidMount() {
    console.log('Session - > Mounting');

    const id = this.props.params.patientId;

    this.getProfile();

    this.getSessionsToPlot();



    this.setState({patient_id: id,
      width: window.innerWidth ,
      height:window.innerHeight,

    });
  }

  componentWillUnmount() {
    console.log('Session - > Unmounting');
  }

  getProfile() {
    Api.patientProfile(this.props.params.patientId).then(profile => {
      this.setState({
        personal_details:profile[0],
      });
    });

  }

  getSessionFrequency(){
    Api.getFrequencyPerWeek(this.props.params.patientId).then(freq => {

      var j =0;
      var total;
      var total_weeks;
      total_weeks = freq.length;
      var frequency;


      frequency = Number(this.state.total_num_sessions)/Number(total_weeks);
      //console.log(frequency)
      this.setState({
        session_frequency : Number(frequency),
      });
    })
    this.getMaxangle();
  }
  getMaxangle(){
    Api.getMaxangle(this.props.params.patientId).then(angle => {

      this.setState({
        max_session_angle:angle[0].max_angle,
      });
  });
}
  getSessionsToPlot(){
    Api.allSessionsgraph(this.props.params.patientId).then(sessiongraph => {
      let i = 0;
      var num;
      var cumulative_dur = 0; //seconds
      var average;

      for (i = 0; i < sessiongraph.length; i++) {
        cumulative_dur = cumulative_dur + Number(sessiongraph[i].duration)
        sessiongraph[i].name = '';
        sessiongraph[i].ses_dur = '';
        sessiongraph[i].cumulative_duration = '';
        sessiongraph[i].cumulative_duration_hour = '';
        sessiongraph[i].label = '';
        sessiongraph[i].cumulative_duration_string = '';
        sessiongraph[i].ses_dur = transform_time(Number(sessiongraph[i].duration));
        sessiongraph[i].name = `Session ${i+1}`;
        sessiongraph[i].label = `S${i+1}`;
        sessiongraph[i].cumulative_duration = cumulative_dur;
        sessiongraph[i].cumulative_duration_hour = convertSecToH(Number(cumulative_dur));
        sessiongraph[i].cumulative_duration_string = convertSecToHM(Number(cumulative_dur));

      }
      var length  = sessiongraph.length
      average = ((sessiongraph[length -1 ].cumulative_duration) / length ) ;
      average = convertSecToHM(average);
      console.log("Average ",average)
      this.setState({
        sessiongraph,
        total_num_sessions : Number(sessiongraph.length),
        average_session_dur: average,
        cumulative_duration: convertSecToHM(Number(cumulative_dur)),
        last_session_date: sessiongraph[length-1].timestamp
      });
    })
    this.getSessionFrequency();
    this.getSession();

  }

  getSession() {
    Api.allSessions(this.props.params.patientId).then(sessions => {
      let i = 0;
      var number;

      for (i = 0; i < sessions.length; i++) {

        sessions[i].name = '';
        sessions[i].ses_dur = '';
        sessions[i].label = '';
        sessions[i].ses_dur = transform_time(Number(sessions[i].duration));
        number = sessions.length - i;
        sessions[i].name = `Session ${number}`;
        sessions[i].label = `S${number}`;

        if (i == (sessions.length - 1)) {
          this.setState({
            sessionSelected: viewAll(sessions),
            sessions,

          });
        }
      }

      this.getCycles();
    });
  }

  getCycles() {
    Api.allCycles(this.props.params.patientId).then(cycles => {
      let i = 0;
      let old = 0;
      let current = 0;
      let session = this.state.sessions.length;


      for (i = 0; i < cycles.length; i++) {
        current = cycles[i].session_id;
        if (current != old) {
          old = current;
          cycles[i].session_name = '';
          cycles[i].session_name = `Session ${session}`;
          session--;
        }
        if (i == (cycles.length - 1)) {
          this.setState({
            cycles,
            data: filterData(cycles,this.state.sessionSelected ,this.state.sessions[0].id),
          });
        }
      }
    });
  }


  ClickOnSessionList(event) {
    const value = event.target.value;

    let updated = [];
    updated = arrayAddRemove(this.state.sessionSelected, Number(value));
    if (updated != this.sessionSelected) {
      this.setState({
        all:false,
        sessionSelected: updated,
        data: filterData(this.state.cycles,updated,this.state.sessions[0].id),
      });
    }
  }

  OnClickSelectAll(event) {
    const target = event.target;
    const checked = target.checked;

    if (checked) {
      this.setState({
        all:checked,
        sessionSelected:viewAll(this.state.sessions),
        data: filterData(this.state.cycles,viewAll(this.state.sessions),this.state.sessions[0].id),
      });
    }    else {
      this.setState({
        all:(checked),
        sessionSelected:[],
        data:[],
      });
    }
  }


  setMargins() {
    this.setState({
      top: 0.05 * (this.state.width),
      left: 0.05 * (this.state.width),
      right: 0.05 * (this.state.width),
      bottom:0.05 * (this.state.width),
    });
  }

  onRadioBtnClick = (rSelected) => {
    this.setState({ rSelected });
    if (rSelected == 1) {
      this.viewSession();
    }    else if (rSelected == 2) {
      this.viewSummary();
    }
  }

  viewSummary() {
    this.setState({
      summary:true,
      session:false,
      title:'Summary of Patient Session Progress',
    });
  }

  viewSession() {
    this.setState({
      summary:false,
      session:true,
      title:'Session Data (Filter By Legend)',
    });
  }

  createCustomSearchField = (props) => (
      <SearchField
        className={'my-custom-class '}
        defaultValue={props.defaultSearch}
        style={{
          borderRadius: '20px',
        }}
        placeholder={props.searchPlaceholder}
      />
    )

  handleMouseEnter = () => {
    const { opacity } = this.state;

    this.setState({
      opacity: { ...opacity, [dataKey]: 0 },
    });
  }

  cellButton(row) {
    return (
      <i className={pain_rating(row)} style={pain_rating_color(row)} />
    );
  }


  render() {
    return (
      <div className="row">
        <div className={'col-lg-12 '}>
          <Breadcrumb  >
            <Breadcrumb.Item style={{color:'#22999D'}} href="/">Home</Breadcrumb.Item>
            <Breadcrumb.Item active>Session History</Breadcrumb.Item>
          </Breadcrumb>
        </div>

        <div className="row">
          <div className="col-lg-5" style={{paddingLeft:'2%' , paddingTop:'2%'}}>
            <Panel>

              <div className={`col-lg-5 ${s.rectangle}`}>
                <i className="fa fa-user-circle fa-5x greyincolor" aria-hidden="true" />
              </div>
              <div className="col-lg-6">
                {<h3 ><strong>{`${this.state.personal_details.name} ${this.state.personal_details.surname}`}</strong></h3>}
                <p><strong>Age: </strong>{this.state.personal_details.age}</p>
                <p><strong>Gender:</strong> {this.state.personal_details.gender}</p>
                <p><strong>ID number:</strong> {this.state.personal_details.id_number}</p>
              </div>
            </Panel>
          </div>

            <div  className="col-lg-7 "style={{paddingLeft:'2%' ,paddingRight:'2%' , paddingTop:'2%'}}>
              <Panel >
                  <h3 className = "text-center"><strong>Patient Progress Overview</strong></h3>
                  <div className = "row">
                    <div className="col-lg-6">
                      <p><strong>Total number of sessions: </strong>{this.state.total_num_sessions}</p>
                      <p><strong>Frequency of sessions:</strong> {this.state.session_frequency} sessions per week</p>
                      <p><strong>Total Cumulative duration :</strong> {this.state.cumulative_duration}</p>
                    </div>
                    <div className="col-lg-6">
                      <p><strong>Maximum Angle over all sessions: </strong>{this.state.max_session_angle}</p>
                      <p><strong>Average Session Duration:</strong> {this.state.average_session_dur}</p>
                      <p><strong>Date of last session: </strong> {this.state.last_session_date}</p>
                    </div>
                  </div>
              </Panel>

      </div>
      </div>


        <div className="row">

          <div className={'col-lg-12'} style={{padding:'2%'}}></div>
          </div>

        <div className="row">

          <div className={'col-lg-12'} style={{padding:'2%'}}>
            <Panel >
              {(this.state.summary) && (

                <div className={'col-lg-10'} >
                  <h3 className="text-center">Summary of Patient Progress</h3>
                  <AreaChart
                    width={(this.state.width) * 0.7} height={0.28 * (this.state.width)} data={this.state.sessiongraph}
                    margin={{ top: 20, right: 0, left: 30, bottom: 20}}
                  >
                    <XAxis dataKey="label">
                      <Label offset={-20} position="insideBottom" >Session Number</Label>
                    </XAxis>
                    <YAxis >
                      <Label offset={10} angle={-90} style={{ textAnchor: 'middle' }} position="insideLeft">Angle [Degrees]</Label>
                    </YAxis>
                    <CartesianGrid strokeDasharray="3 3" />
                    <Tooltip  content={<CustomTooltipSummary/>}/>
                    <Area type="linear" dataKey="max"  stroke="#22999D" fillOpacity={0.7} fill="#22999D" />
                    <Area type="linear" dataKey="min"  stroke="#60a4a6" fillOpacity={0.5} fill="#93f2f5" />
                  </AreaChart>
                </div>
              )}
              {(this.state.session) && (
                <div className={'col-lg-10'} style={{padding:'2%'}}>
                  <Panel >
                    <h3 className="text-center">Session Data</h3>

                    <LineChart
                      width={(this.state.width) * 0.7}
                      height={0.28 * (this.state.width)}
                      margin={{top: 20, right: 50, left: 30, bottom: 20}}
                    >
                      <XAxis dataKey="cycle" type="number" allowDuplicatedCategory={false} domain={['auto', 'auto']} >
                        <Label offset={-15} position="insideBottom" >Cycles</Label>
                      </XAxis>
                      <YAxis dataKey="max" >
                        <Label offset={10} angle={-90} style={{ textAnchor: 'middle' }} position="insideLeft">Maximum Angle [Degrees]</Label>
                      </YAxis>
                      <CartesianGrid strokeDasharray="3 3" />
                      <Tooltip content ={<CustomTooltipSession/>}/>
                      {this.state.data.map(element => (
                        <Line  type="linear" dataKey="max" data={element.data} name={element.name} key={element.name} stroke={element.color} />
                      ))}
                    </LineChart>
                  </Panel>
                </div>
                )}
              <div className={'col-lg-2 text-center'} style={{padding:'2%'}}>
                <Panel width={'100%'} >
                  <h4 >Select Session View</h4>
                  <ButtonGroup>
                    <Button
                      className={s.radioButtonLeft}
                      onClick={() => this.onRadioBtnClick(1)}
                      active={this.state.rSelected === 1}
                    >
                         Session
                      </Button>
                    <Button
                      className={s.radioButtonRight}
                      onClick={() => this.onRadioBtnClick(2)}
                      active={this.state.rSelected === 2}
                    >
                         Summary
                      </Button>
                  </ButtonGroup>
                  {(this.state.session) && (
                    <FormGroup height="200px" controlId="formControlsSelectMultiple"  style={{margin:'20px'}}>
                      <ControlLabel>Select Sessions</ControlLabel>
                      <Checkbox  key={'all'} onChange={this.OnClickSelectAll} name={'selectAll'} checked={this.state.all} value={'all'}>{'Select All'} </Checkbox >
                      {this.state.sessions.map(e =>
                        <Checkbox checked={checked(this.state.sessionSelected,e.id)} key={e.id} onChange={this.ClickOnSessionList} name={e.name} value={e.id}>{e.name}</Checkbox >
                      )
                    }
                    </FormGroup>
                  )}
                </Panel>
              </div>
            </Panel>
          </div>
         <div className={'col-lg-12 text-center'} style={{padding:'2%'}}>
            <Panel>
              <h3>Cumulative Session Duration</h3>
                  <AreaChart
                    width={(this.state.width) * 0.9} height={0.28 * (this.state.width)} data={this.state.sessiongraph}
                    margin={{ top: 20, right: 0, left: 30, bottom: 20}}
                  >
                    <XAxis dataKey="label">
                      <Label offset={-20} position="insideBottom" >Session Number</Label>
                    </XAxis>
                    <YAxis >
                      <Label offset={10} angle={-90} style={{ textAnchor: 'middle' }} position="insideLeft">Duration (Hours) </Label>
                    </YAxis>
                    <CartesianGrid strokeDasharray="3 3" />
                    <Tooltip content ={<CustomTooltipCumulative/>} />
                    <Area type="linear" dataKey="cumulative_duration_hour"  stroke="#22999D" fillOpacity={0.0} fill="#22999D" />
                  </AreaChart>
            </Panel>
          </div>

          <div className={'col-lg-12 text-center'} style={{padding:'2%'}}>
            <Panel>
              <h3>Session Data</h3>
              <BootstrapTable
                data={this.state.sessions}
                options={{
                  noDataText:'noData',
                  searchField: this.createCustomSearchField,
                }}
                hover
                search
                pagination
              >
                <TableHeaderColumn dataField="name" isKey  >Session </TableHeaderColumn>
                <TableHeaderColumn dataField="timestamp"> Date</TableHeaderColumn>
                <TableHeaderColumn dataField="min" >Minimum angle</TableHeaderColumn>
                <TableHeaderColumn dataField="max" >Maximum angle</TableHeaderColumn>
                <TableHeaderColumn dataField="ses_dur" >Duration</TableHeaderColumn>
                <TableHeaderColumn dataField="cycles" >Cycles</TableHeaderColumn>
               <TableHeaderColumn
                  dataField="rating"
                  dataFormat={this.cellButton.bind(this)}
                  editable={false}
                  width="100px"
                >Pain rating </TableHeaderColumn>
              </BootstrapTable>
            </Panel>
          </div>
        </div>
      </div>
    );
  }
}



export default withStyles(s)(Session);



