import React from 'react';
import App from '../components/App';

// Child routes
import home from './home';
import login from './login';
import register from './register';
import practitioner from './practitioner';
import dashboard from './dashboardPages';
import patient from './patient'
import error from './error';
import { Provider } from 'react-redux'
import configureStore from '../redux/reducer';
const store = configureStore();
import Header from '../components/Header';

export default [

  {
    path: '/login',
    children: [
      login,
    ],
    async action({ next, render, context,redirect, params }) {
      const component = await next();
      if (component === undefined) return component;
      return render(
        <App context={context}>{component}</App>
      );
    },
  },
  {
    path: '/register',
    children: [
      register
    ],
    async action({ next, render, context ,redirect, params}) {
      const component = await next();
      if (component === undefined) return component;
      return render(
        <App context={context}>{component}</App>
      );
    },
  },


  {
    path: '/',

  // keep in mind, routes are evaluated in order
    children: [
      home,
      dashboard,
      error,
      patient,
      practitioner,
    ],

    async action({ next, render, context,redirect, params }) {
      const component = await next();

      if (component === undefined) return component;

     console.log('inside dashboard',context);
     if(context.server)
     {
       if(!context.user)
       {
          console.log('not logged in');
          return redirect('/login');
       }
     }
  //   else
     {
        return render(
          <div>
          <Provider store = {store} >
           <div>
            <Header/>
            <div id="page-wrapper" className="page-wrapper">
              <App className="container"  context={context}>{component}</App>
            </div>
            </div>
          </Provider>
          </div>
        );
      }
    },
  },
  {
    path: '/error',
    children: [
      error,
    ],
    async action({ next, render, context }) {
      // console.log('inside error');
      const component = await next();
      // console.log('inside error with component', component);
      if (component === undefined) return component;
      return render(
        <App context={context}>{component}</App>
      );
    },
  },
];



