import React, { Component } from 'react';
// import { Panel, Input, Button } from 'react-bootstrap';
import Button from 'react-bootstrap/lib/Button';
import Panel from 'react-bootstrap/lib/Panel';
import {
  Modal,
  FormControl,
  Spinner,
} from 'react-bootstrap';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from '../styles.css';
import history from '../../core/history';
import Api from '../../api/client';

// function submitHandler(e) {
//   e.preventDefault();
//   history.push('/');
// }
export class Register extends Component {
  constructor(props) {
    super(props);
    this.state = {
      confirm_password: '',
      password: '',
      match: false,
      loading: false,
      submit: false,
      reason: '',
      questions:[],
    };

    this.handleSubmitRegister = this.handleSubmitRegister.bind(this);

    this.handleClose = this.handleClose.bind(this);
    this.getQuestions = this.getQuestions.bind(this);
  }
  componentDidMount() {
    console.log('REGISTER - > Mounting');
    this.getQuestions();

  }
  componentWillUnmount() {
    console.log('REGISTER - > Unmounting');
  }

  handleClose() {
    this.setState({
      submit: false,
    });
  }
  getQuestions(){
    Api.getSecurityQuestions().then(ret =>{

      var i = 0;
      var questionList = [];
      ret.forEach((d,i)=>{
        questionList[i] = {}
        questionList[i].value = d.id
        questionList[i].text = d.question
      })
      this.setState({
        questions:questionList
      })

    })
  }

  handleSubmitRegister() {
    this.setState({ submit: true });

    if (this.state.password == this.state.confirm_password) {

      event.preventDefault();
      let form = document.getElementById('register');


      let user = {};
      user.name = form.name.value;
      user.surname = form.surname.value;
      user.email = form.email.value;
      user.password = form.password.value;
      user.practice_number = form.practitionNr.value;
      user.security_question = form.question.value;
      user.security_answer = form.answer.value;

      this.setState({ loading: true, submit: true });

      Api.Register(user).then(async res => {
          await res;

          if (res.resp) {
          this.setState({
            loading: false,
            success: true,
          });
          history.push('/login');
        }
          else if (res.error) {
          this.setState({
            loading: false,
            success: false,
            reason: 'SEQUELIZE ERROR',
          });
        }

        });
    }
    else {

      this.setState({ success: false, reason: 'Passwords does not match' });
    }
  }

  render() {

  return (
    <div className="col-md-4 col-md-offset-4">
      <div className="text-center">
        <h1 className="login-brand-text" />

        <img style ={{ height: '210px', width: '400px' }} src="/logo.png" alt="MotusCPM" title="MotusCPM" />
        {/* <h3 className="text-muted">Created by <a href="http://startreact.com">StartReact.com</a> team</h3> */}
      </div>

      <Panel header={<h3>Please fill in all of the below</h3>} className="login-panel">

        {/* <form role="form" onSubmit={(e) => { submitHandler(e); }}>
          <form  role="form" method="POST" action="api/user/register" >*/}

        <form id ="register">
            <fieldset>
            <div className="form-group">
            <label>Name</label>
              <FormControl
                type="text"
                className="form-control"
                placeholder="Name"
                name="name"
              />
            </div>

            <div className="form-group">
            <label>Surname</label>
              <FormControl
                type="text"
                className="form-control"
                placeholder="Surname"
                name="surname"
              />
            </div>

            <div className="form-group">
            <label>Email address</label>
              <FormControl
                type="text"
                className="form-control"
                placeholder="Email"
                name="email"
              />
            </div>
            <div className="form-group">
            <label>Password</label>
              <FormControl
                type="text"
                className="form-control"
                placeholder="Password"
                type="password"
                name="password"
                onChange={(e) => this.setState({ password: e.target.value })}
              />
            </div>


            <div className="form-group">
            <label>Confirm password</label>
              <FormControl
                className="form-control"
                placeholder="Confirm Password"
                type="password"
                name="confirm"
                onChange={(e) => this.setState({ confirm_password: e.target.value })}
              />

            </div>

            <div className="form-group">
            <label>Practice number</label>
              <FormControl
                className="form-control"
                placeholder="Practicing Number"
                name="practitionNr"
              />

            </div>
            <div className="form-group">
            <label>Security question</label>
            <FormControl
              componentClass="select"
              className="form-control"
              placeholder="Select a security question"
              name = "question"
              >
              {this.state.questions.map((question)=>
                <option key ={question.value}
                  value = {question.value}>
                  {question.text}
                </option>
              )}
            </FormControl>
            </div>

              <div className="form-group">
              <label>Security answer</label>
                <FormControl
                  type = "text"
                  className="form-control"
                  placeholder="Security Answer"
                  name="answer"
                />

            </div>
            {/* <Checkbox label="Remember Me" > Remember Me </Checkbox> */}

          </fieldset>
          </form>
        <Button type="submit" bsSize="large" className ={s.PrimaryButton} onClick={this.handleSubmitRegister} block>Register</Button>
        <Button className ={s.SecondaryButton} type="submit" bsSize="small" block onClick ={() => history.push('/login')}>Cancel</Button>
      </Panel>


      <Modal show= {this.state.submit} onHide={this.handleClose}>
        <Modal.Body>
         {(this.state.loading) && (
         <div>
           <Spinner
             as="span"
             animation="border"
             size="sm"
             role="status"
             aria-hidden="true"
           />
          Loading...
         </div>
       )}
         {(!this.state.loading) && (
         <div>
           {this.state.success ? 'Registration successful: ': this.state.reason}
         </div>
       )}
       </Modal.Body>

      </Modal>

    </div>

  );
}
}




export default withStyles(s)(Register);



