import React, { PropTypes,Component } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Home.css'
import { transformSeconds , pain_rating, pain_rating_color, transform_time} from '../../core/Utilities'
import {
  MenuItem,
  DropdownButton,
  Panel, PageHeader, ListGroup, ListGroupItem, Button,Modal, FormControl
} from 'react-bootstrap';

import { BootstrapTable, TableHeaderColumn,SearchField ,InsertButton} from 'react-bootstrap-table';

import Api from '../../api/client'
import {
  Tooltip,
  XAxis, YAxis, Area,
  CartesianGrid, AreaChart, Bar, BarChart,
  ResponsiveContainer } from '../../vendor/recharts';


  import { setUser } from '../../redux/reducer'
  import { connect } from 'react-redux'

const title = 'Medev Home Page';
const data_array = [];




class Home extends Component {

  constructor(props) {
//console.log("props",props);
    super(props);
    this.state={
      data : [],
      modal:false,
      found:false,
      body:'',
      results:false,
      patientNew:[]

    }
    this.getPatients = this.getPatients.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.NewPatient = this.NewPatient.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.handlelinking = this.handlelinking.bind(this);
    this.showModal = this.showModal.bind(this);

  }

    componentDidMount() {
    console.log("HOME - > Mounting")
    this.getPatients();
    }

    componentWillUnmount() {
      console.log("HOME -> unmounting")
    }


    getPatients(){

      Api.patientList().then(list =>{
        var i = 0;
        for(i = 0 ; i<list.length ; i++){
          list[i].fullname = ""
          list[i].last_session = ""
          list[i].fullname = list[i].name + " " + list[i].surname;
          list[i].last_session = transformSeconds(list[i].last_session_sec)
          list[i].ses_dur = "";
          list[i].ses_dur = transform_time(Number(list[i].duration))
        }
        this.setState({data:list})

        let data_array = list;

      })

    }

    handleClose(){
      this.setState({
        modal:(!this.state.modal),
        found:false, results: false, body:''
      });
    }
    handlelinking(){
      console.log("Linking patient to practitioner")
      Api.linkPatient(this.state.patientNew).then(result =>{
          this.setState({
            modal:false,
            found:false,
            results:true,
            body: "Patient Successfully linked"
          })
          this.getPatients();
      })
    }

    handleSearch(){
      var form = document.getElementById('add-patient');

      var id_number = form.id_number.value
      Api.searchPatient(id_number).then(patient=>{

        if (patient.length > 0){
          this.setState({
            patientNew: patient[0],
            modal:false,
            found:true,
            results:true,
            body: "Is " + patient[0].name + " " + patient[0].surname + " your patient?"
          })
        }
        else{
          this.setState({
            modal:false,
            found:false,
            results:true,
            body: "Patient does not exist"
          })
        }
      })

    }

    showModal(){
      this.setState({ modal: true, found: false , results: false})
    }

    NewPatient(){
      return(
        <div>
        <Modal show = {this.state.modal} onHide={this.handleClose}>
            <Modal.Header closeButton style = {{ backgroundColor:'#4D4D4D'}}>
                <img className = {s.logo} src='/logo.png' alt="MotusCPM" title="MotusCPM" />
          </Modal.Header>
          <Modal.Body>
            <form id = "add-patient">
              <fieldset>
                <div className="form-group">
                    <label>Please enter patient id number</label>
                    <FormControl
                      type="text"
                      className="form-control"
                      name="id_number"
                    />
                </div>
              </fieldset>
            </form>
          </Modal.Body>
          <Modal.Footer>
            <Button  type="submit" bsSize="large" className ={s.modalButton2} onClick={this.handleSearch}>Search</Button>
          </Modal.Footer>
        </Modal>


          <Modal show = {this.state.results} onHide={this.handleClose}>

            <Modal.Body>
              <h2 className = "text-center">{this.state.body}</h2>
            </Modal.Body>
            <Modal.Footer>
            <div >
              <Button  type="submit" bsSize="large" className = {this.state.found? s.modalButton:s.modalButton2 } onClick={this.state.found ? this.handlelinking : this.showModal}>{this.state.found ? 'YES' :'OK'}</Button>
              {(this.state.found)&&( <Button  type="submit"  bsSize="large" className = {s.modalButton } onClick={this.showModal}>NO</Button>)}
            </div>
            </Modal.Footer>
          </Modal>

        </div>
      );
    }


    isExpandableRow(row) {
    if (row.user_id) return true;
    else return false;
  }

  expandComponent(row) {
   return (
     <div className = "row">

       <div className = "col-lg-5">

           <ListGroup>
              <ListGroupItem  style = {{padding:'3px'}}><strong>ID number:  </strong> {row.id_number}</ListGroupItem>
              <ListGroupItem  style = {{padding:'3px'}}><strong>Age:   </strong> {row.age}</ListGroupItem>
              <ListGroupItem  style = {{padding:'3px'}}><strong>Gender:  </strong> {row.gender}</ListGroupItem>
              <ListGroupItem  style = {{padding:'3px'}}><strong>Date registered:  </strong> {row.date_added}</ListGroupItem>
              <ListGroupItem  style = {{padding:'3px'}}><strong>Contact number:  </strong> {row.mobile_number}</ListGroupItem>
           </ListGroup>
         <Button  bsSize="large"  className = {s.PrimaryButton} href={"/patient/"+ row.user_id + "/profile"}  block>Go To Patient Profile </Button>
       </div>

       <div className = "col-lg-2">
       </div>
       {(row.max> 0)&&(
       <div className = "col-lg-5">
          <p className = {s.text}>Session summary</p>
         <ListGroup>
          <ListGroupItem style = {{padding:'3px'}}><strong>Last session:</strong> {row.last_session_date}</ListGroupItem>
          <ListGroupItem style = {{padding:'3px'}}><strong>Max angle:</strong> {row.max}</ListGroupItem>
          <ListGroupItem  style = {{padding:'3px'}}><strong>Session Duration:</strong> {row.ses_dur}</ListGroupItem>
          <ListGroupItem  style = {{padding:'3px'}}><strong>Total cycles:</strong> {row.cycles}</ListGroupItem>
          <ListGroupItem  style = {{padding:'3px'}}><strong>Pain rating:</strong><i className = { pain_rating(row.rating) } style = {pain_rating_color(row.rating)}></i> </ListGroupItem>
         </ListGroup>
         <Button  bsSize="large"  className = {s.PrimaryButton}  href={"/patient/"+ row.user_id + "/history"}block>View Session History</Button>
       </div>
       )}
       {(row.max< 1)&&(
       <div className = "col-lg-5">
          <p className = {s.text}>No Session Data</p>
        </div>
        )}

      </div>

   );
 }

 createCustomSearchField = (props) => {
     return (
       <SearchField
          className={'my-custom-class ' }
         defaultValue={ props.defaultSearch }
         style={ {
           borderRadius: '20px',
          } }
         placeholder={ props.searchPlaceholder }/>
     );
   }


  render(){
    console.log("Home-> Render")


   const createCustomInsertButton = (onClick) => {
    return (
      <InsertButton
        btnText='Add'
        btnContextual='btn-primary'
        style = {{backgroundColor : '#22999D' ,  color:'#FFFFFF' , width:'100%', borderRadius:'20px'}}
        // btnGlyphicon='glyphicon-edit'
        onClick={ this.showModal}/>
    );
  }

  return (
    <div>
       <div className="row">
        <div className="col-lg-12" >
          <h3>Patient list</h3>
        </div>
      </div>
      {this.NewPatient()}
       <div className="row">
      <div className={"col-lg-12 "} style = {{borderRadius :'20px'}}>
      <BootstrapTable
            className = {s.table}
            data={ this.state.data}
            options={ { expandRowBgColor: '#4D4D4D',
             noDataText:"noData",
             searchField: this.createCustomSearchField,
             insertBtn: createCustomInsertButton}
            }
            expandableRow={ this.isExpandableRow }
            expandComponent={ this.expandComponent }
            hover={true}
            search
            insertRow={true}
          >
          <TableHeaderColumn dataField='fullname' expandable ={true} isKey={ true}  >Patient Name </TableHeaderColumn>
          <TableHeaderColumn dataField='id_number' expandable ={true}   >Patient ID Number </TableHeaderColumn>
          <TableHeaderColumn dataField='last_session'  > Last Session</TableHeaderColumn>
        </BootstrapTable>
        </div>
        </div>

    </div>
    )
  }
}


const mapStateToProps = (state) => {

  return {store: state}
}

const mapDispatchToProps = (dispatch) => {
  return {
    add: (user)=>  {
      dispatch (setUser(user))
    }

  }
}

export default connect (mapStateToProps,mapDispatchToProps)(withStyles(s)(Home))


//export default Home;



