import React, { PropTypes,Component } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from '../styles.css';
import {
  MenuItem,
  DropdownButton,Modal, FormControl, FormGroup,Breadcrumb,
  Panel, PageHeader, ListGroup, ListGroupItem, Button,OverlayTrigger, Popover
} from 'react-bootstrap';

import { BootstrapTable, TableHeaderColumn,SearchField,InsertButton } from 'react-bootstrap-table';
import history from '../../../core/history';
import Api from '../../../api/client'
import {
  Tooltip,
  XAxis, YAxis, Area,
  CartesianGrid, AreaChart, Bar, BarChart , Legend,
  ResponsiveContainer ,PieChart, Pie, Sector, Cell } from '../../../vendor/recharts';


const title = "Patient Profile";



class Profile extends Component {
  constructor(props){
    super(props);
    this.state = {
      medical_log:[],
      personal_details:[],
      patient_id:-1,
      modal: false,
      add:false,
      adding: false,
      value:'',
      message:'',
      popup:false

    }

    this.LogInjury = this.LogInjury.bind(this);
    this.handleAdd = this.handleAdd.bind(this);
    this.showModal = this.showModal.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.getMedicalLog = this.getMedicalLog.bind(this);
    this.getProfile = this.getProfile.bind(this);

  }

  componentDidMount(){
    console.log("Profile - > Mounting");

    var id = this.props.params.patientId
    this.setState({patient_id: id})
    this.getMedicalLog();
    this.getProfile();
  }

  componentWillUnmount(){
    console.log("Profile - > Unmounting");
  }

  getMedicalLog(){
    Api.medicalLog(this.props.params.patientId).then(medics =>{
      var i =0;
      for(i = 0 ; i<medics.length; i++){
        var DBdate = new Date(medics[i].first_medical_attention)
        var currdate = new Date("01/01/2019")
        if(DBdate < currdate){
          medics[i].first_medical_attention = "No date selected"
        }
      }

      this.setState({
        medical_log:medics,
      })
    })
  }

  getProfile(){
    Api.patientProfile(this.props.params.patientId).then(profile =>{

      this.setState({
        personal_details:profile[0]
      })
    })
  }
  showModal(){
    this.setState({modal:true})
  }

  handleAdd (){
    var form = document.getElementById('log-injury');

    var newLog = {}
    newLog.description = form.description.value;
    newLog.location = form.location.value;
    newLog.med_attention = form.first_medical_attention_received.value;
    newLog.user_id = this.props.params.patientId;
    this.setState({adding:true})
    Api.logInjury(newLog).then(res =>{
      this.setState({
        adding:false,
        popup:true,
        modal:false,
        message:"Successfully logged entry!"
      })
      this.getMedicalLog();
    })


  }

  handleClose(){
    this.setState({
      add: false, adding:false,
      modal:false,popup:false,
    })
  }

  getValidationState() {
    const length = this.state.value.length;
    if (length > 0) return 'success';

    else return 'error';

    return null;
  }

  handleChange(e){
    this.setState({value: e.target.value})
  }

  LogInjury(){
    return(
      <div>
      <Modal show = {this.state.modal} onHide={this.handleClose}>
          <Modal.Header closeButton style = {{ backgroundColor:'#4D4D4D'}}>
              <img className = {s.logo} src='/logo.png' alt="MotusCPM" title="MotusCPM" />
        </Modal.Header>
        <Modal.Body>
          <form id = "log-injury">
            <fieldset>

              <FormGroup
                controlId="formBasicText"
                >
                  <label>Description of injury/pathology </label>
                  <FormControl
                    type="text"
                    className="form-control"
                    placeholder = "eg. Fractured ankle [left]"
                    name="description"
                    onChange = {this.handleChange}
                  />
              </FormGroup>

              <FormGroup
                controlId="formBasicText"
                >
                  <label>Surgical Procedure (if applicable) </label>
                  <FormControl
                    type="text"
                    placeholder = "eg. Panorama Mediclinic "
                    className="form-control"
                    onChange = {this.handleChange}
                    name="location"
                  />
              </FormGroup>
              <div className="form-group">
                  <label>Date of surgery (if applicable) </label>
                  <FormControl
                    type="date"
                    className="form-control"
                    name="first_medical_attention_received"
                  />
              </div>

            </fieldset>
          </form>
        </Modal.Body>
        <Modal.Footer>
          <Button  type="submit" bsSize="large" style = {{backgroundColor:'#22999D' , color:'#FFFFFF', width:'20%' , borderRadius:'20px',border:'1px solid #22999D', margin:'0 auto' , padding:'5px'}} onClick={this.handleAdd} disabled ={this.state.adding}>{this.state.adding? 'Logging...': 'Log'}</Button>
        </Modal.Footer>
      </Modal>

      <Modal show = {this.state.popup} onHide={this.handleClose}>
        <Modal.Body>
        <h3 className = "text-center">{this.state.message}</h3>
        </Modal.Body>
        <Modal.Footer>
          <Button  type="submit" bsSize="large" style = {{backgroundColor:'#22999D' , color:'#FFFFFF', width:'20%' , borderRadius:'20px',border:'1px solid #22999D', margin:'0 auto' , padding:'5px'}} onClick={this.handleClose} >OK</Button>
        </Modal.Footer>
      </Modal>
      </div>
    );
  }


  createCustomSearchField = (props) => {
      return (
        <SearchField
           className={'my-custom-class ' }
          defaultValue={ props.defaultSearch }
          style={ {
            borderRadius: '20px',
           } }
          placeholder={ props.searchPlaceholder }/>
      );
    }

  render(){


    const createCustomInsertButton = (onClick) => {
     return (
       <InsertButton
         btnText='Add'
         btnContextual='btn-primary'
         style = {{backgroundColor : '#22999D' ,  color:'#FFFFFF' , width:'100%', borderRadius:'20px'}}
         // btnGlyphicon='glyphicon-edit'
         onClick={ this.showModal}/>
     );
   }

    return(
      <div className = "row">
      <div className ={ "col-lg-12 " }>
        <Breadcrumb  >
          <Breadcrumb.Item style = {{color:'#22999D'}} href="/">Home</Breadcrumb.Item>

          <Breadcrumb.Item active>Patient profile</Breadcrumb.Item>
        </Breadcrumb>
      </div>

        <div className = "row">
          <div className = "col-lg-4" style = {{paddingLeft:'2%' ,paddingRight:'2%',  paddingBottom:'5%'}}>
            <h3 className = "text-left">Personal Information</h3>
             <ListGroup>
                <ListGroupItem  style = {{padding:'3px'}}><strong>First name:</strong>{this.state.personal_details.name}</ListGroupItem>
                <ListGroupItem  style = {{padding:'3px'}}><strong>Last Name:</strong> {this.state.personal_details.surname}</ListGroupItem>
                <ListGroupItem  style = {{padding:'3px'}}><strong>Age:</strong> {this.state.personal_details.age}</ListGroupItem>
                <ListGroupItem  style = {{padding:'3px'}}><strong>Gender:</strong> {this.state.personal_details.gender}</ListGroupItem>
                <ListGroupItem  style = {{padding:'3px'}}><strong>Date registered:</strong> {this.state.personal_details.created_at}</ListGroupItem>
                <ListGroupItem  style = {{padding:'3px'}}><strong>Contact number:</strong> {this.state.personal_details.mobile_number}</ListGroupItem>
                <ListGroupItem  style = {{padding:'3px'}}><strong>Email address:</strong> {this.state.personal_details.email}</ListGroupItem>
             </ListGroup>
          </div>
          {this.LogInjury()}
          <div className = "col-lg-8" style = {{paddingLeft:'2%', paddingRight:'2%' , paddingBottom:'5%'}}>
          <h3 className = "text-left">Medical History Log</h3>
            <BootstrapTable
            data={ this.state.medical_log}
            options= {{
                noDataText:"noData",
                searchField: this.createCustomSearchField,
                insertBtn: createCustomInsertButton}}
            hover={true}
            search
            insertRow={true}>
              <TableHeaderColumn dataField='details' isKey={ true}  >Description of injury/pathology</TableHeaderColumn>
              <TableHeaderColumn dataField='location' >Surgical Procedure</TableHeaderColumn>
              <TableHeaderColumn dataField='first_medical_attention' >Date of surgery</TableHeaderColumn>
              <TableHeaderColumn dataField='timestamp'>Logged in system</TableHeaderColumn>
            </BootstrapTable>
          </div>
        </div>
      </div>
    );
  }
}

export default withStyles(s)(Profile)



