
import express from 'express';
import sequelize from '../data/sequelize'
import utf8 from 'utf8';
import crypto  from 'crypto';
import { create } from "apisauce"
var passwordHash = require('password-hash');

function generateHash(password) {
  let iterations = 1;
  let salt = '767684f2';
  let algorithm = 'sha1'
  try {
    var hash = password;
    for(var i=0; i<iterations; ++i) {
      hash = crypto.createHmac('sha1', salt).update(hash).digest('hex');
    }

    //return algorithm + '$' + salt + '$' + iterations + '$' + hash;
    return (algorithm + '$'  + '$'  + '$' + hash);
  } catch (e) {
    throw new Error('Invalid message digest algorithm');
  }
}

var router = express.Router()



function sendSQL_medev(req, res , query, options )
{
  sequelize.query(query, options).spread(myTableRows => {
    console.log(myTableRows[0]);
    res.send(myTableRows[0]);
    },
    reason => {
      console.log('medev -> reason ', reason)
      res.status(400).json({error:'SQL error',
                'sql-err':reason.originalError.info.message});
    }
  );
}




