router.post('/update/password' , function(req,res){
  console.log(req.body.password)

  var password_Hashed = '';

  if(passwordHash.isHashed(req.body.password))
  {
    console.log("Password was hashed");
    password_Hashed = req.body.password;
  }
  else
  {
    console.log("Password was not hashed");
    password_Hashed = generateHash(req.body.password);
  }


  var query = "update users set password = '" +  password_Hashed +"' where id = '" + req.body.user_id+ "'"

  sequelize.query(query).then( results =>{
    res.send(results[0]);
  })

})

router.post('/user/register/', (req, res) => {
  console.log('Registering Medev User')

  sequelize.query("select top 1 * from users where email =' " + req.body.email + "'").spread((users ,metadata)=>
  {

    if(users.length > 0) return res.send({error:"e-mail all ready exits"})

    var security_question  = -1;
    var security_answer = '';
    security_question = req.body.security_question;
    security_answer = req.body.security_answer;

    var password_Hashed = '';
    if(passwordHash.isHashed(req.body.password))
    {
      console.log("Password was hashed");
      password_Hashed= req.body.password;
    }
    else
    {
      console.log("Password was not hashed");
      password_Hashed = generateHash(req.body.password);
    }
    const data = req.body;
    const key_array = Object.keys(data)
    if(key_array.includes('mobile_number')){
      sequelize.query("insert into users (email,password_token,name,surname,mobile_number, id_number, practitioner_name, practice_number, patient) values (:email,:password, :name, :surname, :mobile_number, :id_number, :practitioner_name, :practice_number, 1)",
        {
          replacements:
          {
            email:req.body.email,
            name:req.body.name,
            surname:req.body.surname,
            password:password_Hashed,
            mobile_number:req.body.mobile_number,
            id_number:req.body.id_number,
            practitioner_name:req.body.practitioner_name,
            practice_number:req.body.practice_number
          }
        }).spread((results, metadata) => {

          if (metadata === 1) return res.send({resp:'ok'});
          if (metadata != 1) return res.send({error:'registration failed'})
        });
      }

    else{
      sequelize.query("insert into users (email,password,name,surname,practice_number, practitioner, security_question , security_question_answer) values (:email,:password, :name, :surname, :practice_number, 1 , :security_question , :security_answer)",
        {
          replacements:
          {
            email:req.body.email,
            name:req.body.name,
            surname:req.body.surname,
            password:password_Hashed,
            practice_number:req.body.practice_number,
            security_question:req.body.security_question,
            security_answer:req.body.security_answer
          }
        }).spread((results, metadata) => {

          if (metadata === 1) return res.send({resp:'ok'});
          if (metadata != 1) return res.send({error:'registration failed'})
        });
      }

  });

});


