router.post('/session/create/', (req, res) => {

  var id = 0;
  if (req.body.id > 0) id = req.body.id;
  if (req.body.user_id > 0) id = req.body.user_id;

  sequelize.query("insert into session_log (user_id) values (:user_id)",
    {
      replacements:
      {
        user_id:id
      }
    }).spread((results, metadata) => {

      if (metadata === 1) sendSQL_medev(req, res,'select top 1 * from session_log order by id desc');
      if (metadata != 1) return res.send({error:'creation of session failed'})
    });

});

