export function transformSeconds(seconds){
  var newValue = ""

  if(seconds != null && seconds < 60){
    newValue = seconds + " seconds ago"
  }
  else if(seconds >= 60 && seconds <3600){
      newValue = Math.round((seconds)/60) + " minutes ago"
  }
  else if(seconds >=3600 && seconds <86400 ){
      newValue = Math.round((seconds)/3600) + " hours ago"
  }
  else if(seconds >=86400){
      newValue = Math.round((seconds)/86400) + " days ago"
  }
  else{
      newValue = "no data"
  }

  return(newValue);
}

export function transform_time(seconds){
  var newValue = ""

  if(seconds != null && seconds < 60){
    newValue = seconds + " seconds "
  }
  else if(seconds >= 60 && seconds <3600){
      newValue = Math.round((seconds)/60) + " minutes"
  }
  else if(seconds >=3600 && seconds <86400 ){
      newValue = Math.round((seconds)/3600) + " hours"
  }
  else if(seconds >=86400){
      newValue = Math.round((seconds)/86400) + " days"
  }
  else{
      newValue = "no data"
  }

  return(newValue);
}



export function pain_rating(rating){

  var icon = "";
  if(rating !=null && rating < 0.4){
    icon = "fa fa-smile-o fa-3x "
  }
  else if(rating > 0.4  && rating <0.7){
    icon = "fa fa-meh-o fa-3x "
  }
  else if(rating > 0.7 ){
    icon = " fa fa-frown-o fa-3x "
  }
  else{
    icon = " fa fa-bomb  fa-3x"
  }

  return icon;
}

export function pain_rating_color(rating){
  var style = {}
  if(rating !=null && rating < 0.4){
    style = {color:'green'}
  }
  else if(rating > 0.4  && rating <0.7){
      style = {color:'orange'}
  }
  else if(rating > 0.7 ){
      style = {color:'red'}
    }
  else{
  style = {color:'black'}
  }

  return style;
}


export function arrayAddRemove(array, value){

  if(array.includes(value)){
    //get the index
    var index = array.indexOf(value);
    array.splice(index,1);
    return array;

  }
  else{
    array = array.concat(value)
    return array;
  }
}

export function filterData(dataArray, filter,last_session){
  var newArray = [];
  var newData = [];
  var results = [];
  var color;

  filter.forEach(e =>{
   // console.log(JSON.stringify(e))
        results = dataArray.filter(entry => {
           return entry.session_id == e
        })
    //console.log("results " + JSON.stringify(results))

  if(results.length >0)      var name = results[0].session_name

      if( e == last_session ){
        color ="#fc0303"
      }
      else{
        color = "#22999D"
      }
      newArray = [ {id: e, name: name, color: color, data : results}]
      newData = newData.concat(newArray)
  })

  return newData;
}

export function viewAll(dataArray){

  var selected = [];
  var results = [];
  var i = 0;
  for(i = 0;i < dataArray.length ; i++){

    selected = selected.concat(dataArray[i].id);
  }

  return selected;
}

export function checked(sessionArray,value){
  return sessionArray.includes(Number(value));
}

export function get_max_angle(array){
  var max = -1
  var i = 0;
  var curr = 0
  for(i=0 ; i<array.length ; i++){
    curr =  array[i].max ;
    if(curr> max){
      max = curr
    }
  }
  return max;
}

export function convertSecToHM(inputSecond){
var secondsInput = 0.00;

 var string;
 var hours ;
 var minutes;
 var seconds;

hours = Math.floor(inputSecond / 3600);
inputSecond %= 3600;
minutes = Math.floor(inputSecond / 60);
seconds = inputSecond % 60;

 string = "" + hours + " hour and " + minutes + " minutes";

  return string;
}

export function convertSecToH(inputSecond){
  var hours = 0.00;
  var secondsInHour = 60*60;
  hours = (inputSecond/secondsInHour);

  return hours;
 }



