async function findUser(username)
{
    let ret = null;
    try {
    var d = await sequelize.query("select top 1 * from users where email = :email",{replacements:{email: username}}).then(myTableRows => {

        myTableRows[0].map(row => {

          ret = row;
        })
      });
    }catch(e) {
      //console.log("Error" ,e); // 30
    }
    //  console.log("ret" ,ret); // 30
    return ret;
};
