import React, { Component, PropTypes } from 'react';
// import { Panel, Input, Button } from 'react-bootstrap';
import Button from 'react-bootstrap/lib/Button';
import Panel from 'react-bootstrap/lib/Panel';
import { FormControl, Modal } from 'react-bootstrap';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from '../styles.css';
import history from '../../core/history';


import Api from '../../api/client';

// function submitHandler(e) {
//   e.preventDefault();
//   history.push('/');
// }
class Login extends Component {

  constructor(props) {
    super(props);
    this.state = {
      forgot: false,
      update: false,
      email: false,
      question: '',
      answer: '',
      message: '',
      results: false,
      correct: false,
      user_id: -1,
    };
    this.handleForgotPassword = this.handleForgotPassword.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.handleShow = this.handleShow.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.getForgotPasswordDetails = this.getForgotPasswordDetails.bind(this);
    this.handleSecurity = this.handleSecurity.bind(this);
    this.handleCloseSecurity = this.handleCloseSecurity.bind(this);
    this.handleCloseUpdate = this.handleCloseUpdate.bind(this);
    this.handleCloseResults = this.handleCloseResults.bind(this);
    this.updatePassword = this.updatePassword.bind(this);
  }

  componentDidMount() {
    this.getForgotPasswordDetails();
  }
  componentWillUnmount() {

  }

  getForgotPasswordDetails() {

  }


  handleClose() {
    this.setState({
      forgot: false, email: false,
    });
  }
  handleCloseSecurity() {
    this.setState({
      forgot: false, email: false, message: '', results: false,
    });
  }
  handleCloseUpdate() {
    this.setState({
      results: false, email: false, update: false, results: false, message: '',
    });
  }
  handleCloseResults() {
    this.setState({
      results: false, update: true, message: '',
    });
  }

  handleShow() {
    this.setState({
      email: true,
      forgot: false,
    });
  }

  handleSubmit() {
    const form = document.getElementById('get-email');
    const email = form.email.value;

    Api.searchForgot(email).then(data => {
      const answer = data[0].answer;
      this.setState({
        user_id: data[0].user_id,
        question: data[0].question,
        answer: answer.toLowerCase(),
        email: false,
        forgot: true,

      });
    });
  }

  handleSecurity() {
    const form = document.getElementById('security');
    let answer = form.answer.value;
    answer = answer.toLowerCase();

    if (answer == this.state.answer) {
      console.log('correct answer');
      this.setState({
        forgot: false,
        update: true,
        results: false,
      });
    } else {
      console.log('wrong answer');
      this.setState({
        forgot: true,

      });
    }
  }

  updatePassword() {
    const form = document.getElementById('password');
    const password = form.password.value;
    const confirm = form.confirm.value;

    if (password == confirm) {
      console.log('API update password here');
      Api.updateNewPassword(password, this.state.user_id).then(() => {
        this.setState({
          update: false,
          results: true,
          correct: true,
          message: 'Successfully updated password',
        });
      });
    } else {
      this.setState({
        update: false,
        results: true,
        correct: false,
        message: 'Passwords does not match',
      });
    }
  }


  handleForgotPassword() {
    return (
      <div>
        <Modal show={(this.state.email)} onHide={this.handleClose}>
          <Modal.Header closeButton style={{ backgroundColor: '#4D4D4D' }}>
            <img className={s.logo} src="/logo.png" alt="MotusCPM" title="MotusCPM" />
          </Modal.Header>
          <Modal.Body>

            <form id="get-email">
              <fieldset>
                <div className="form-group">
                  <label>
                    Please insert your email address
                    </label>
                  <FormControl
                    type="text"
                    className="form-control"
                    name="email"
                  />
                </div>
              </fieldset>
            </form>
          </Modal.Body>
          <Modal.Footer>
            <Button type="submit" bsSize="large" className={s.PrimaryButton} onClick={this.handleSubmit}>Submit</Button>
          </Modal.Footer>
        </Modal>
        <Modal show={(this.state.forgot)} onHide={this.handleCloseSecurity}>
          <Modal.Header closeButton style={{ backgroundColor: '#4D4D4D' }}>
            <img className={s.logo} src="/logo.png" alt="MotusCPM" title="MotusCPM" />
          </Modal.Header>
          <Modal.Body>
            <h3>Please answer the security question</h3>
            <form id="security">
              <fieldset>
                <div className="form-group">
                  <label>
                    {this.state.question}{this.state.message}
                  </label>
                  <FormControl
                    type="text"
                    className="form-control"
                    name="answer"
                  />
                </div>
              </fieldset>
            </form>
          </Modal.Body>
          <Modal.Footer>
            <Button type="submit" bsSize="large" className={s.PrimaryButton} onClick={this.handleSecurity}>Submit</Button>
          </Modal.Footer>
        </Modal>
        <Modal show={(this.state.update)} onHide={this.handleCloseUpdate}>
          <Modal.Header closeButton style={{ backgroundColor: '#4D4D4D' }}>
            <img className={s.logo} src="/logo.png" alt="MotusCPM" title="MotusCPM" />
          </Modal.Header>
          <Modal.Body>
            <h3> Please insert your new password</h3>
            <form id="password">
              <fieldset>
                <div className="form-group">
                  <label>
                    New password
                    </label>
                  <FormControl
                    type="password"
                    className="form-control"
                    name="password"
                  />
                </div>
                <div className="form-group">
                  <label>
                    Confirm new password
                    </label>
                  <FormControl
                    type="password"
                    className="form-control"
                    name="confirm"
                  />
                </div>
              </fieldset>
            </form>
          </Modal.Body>
          <Modal.Footer>
            <Button type="submit" bsSize="large" className={s.PrimaryButton} onClick={this.updatePassword}>Submit</Button>
          </Modal.Footer>
        </Modal>

        <Modal show={(this.state.results)} onHide={this.handleCloseUpdate}>
          <Modal.Body>
            {this.state.message}
          </Modal.Body>
          <Modal.Footer>
            <Button type="submit" bsSize="large" className={s.PrimaryButton} onClick={this.state.correct ? this.handleCloseUpdate : this.handleCloseResults}>OK</Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }

// context.setTitle(title);
  render() {
    return (
      <div className={'col-md-4 col-md-offset-4 '}>
        <div className="text-center">
          <h1 className="login-brand-text" />
          {/* <h3 className="text-muted">Created by <a href="http://startreact.com">StartReact.com</a> team</h3> */}
          <img style={{ height: '210px', width: '400px' }}src="/logo.png" alt="MotusCPM" title="MotusCPM" />
        </div>
        {this.handleForgotPassword()}
        <Panel header={<h3 className={s.text}>Please Login</h3>} className={`login-panel ${s.text}`}>

          {/* <form role="form" onSubmit={(e) => { submitHandler(e); }}> */}
          <form role="form" method="POST" action="/login" >
            <fieldset>
              <div className="form-group">
                <FormControl
                  type="text"
                  className={'form-control '}
                  placeholder="Username"
                  name="username"
                />
              </div>

              <div className="form-group">
                <FormControl
                  className="form-control"
                  placeholder="Password"
                  type="password"
                  name="password"
                />
              </div>
              {/* <Checkbox label="Remember Me" > Remember Me </Checkbox> */}
              <Button type="submit" bsSize="large" className={s.PrimaryButton} block>Login</Button>
            </fieldset>
          </form>
          <Button className={s.SecondaryButton} type="submit" bsSize="small" block onClick={() => history.push('/register')}>Create Account</Button>
          <Button className={s.link} type="submit" bsSize="small" onClick={() => this.setState({ email: true })}>Forgot Password?</Button>
        </Panel>
      </div>


    );
  }
}


Login.contextTypes = { setTitle: PropTypes.func.isRequired };

export default withStyles(s)(Login);



